.svg-image-phone-rounded {
  background: url("../images/sprites/main.svg") no-repeat; }

.svg-image-phone-rounded {
  background-position: 0 0;
  background-size: 100% 100%;
  width: 72px; }

.svg-image-phone-rounded:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%; }

.svg-image-phone-rounded-dims {
  width: 72px;
  height: 72px; }

.image-by-height {
  width: auto; }

.image-by-height:before {
  content: none; }

.image-by-height > svg {
  height: inherit; }

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important; }

* {
  min-height: 0;
  min-width: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  min-width: 320px; }

body {
  display: flex;
  flex-direction: column;
  background: #fff;
  font-size: 14px;
  line-height: 24px;
  font-family: "Montserrat", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh; }

a {
  color: inherit;
  transition: 0.2s; }
  a:hover {
    text-decoration: none; }

p {
  margin: 0 0 15px; }

input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0; }

input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none; }

button {
  cursor: pointer; }

[class^="svg-image"], [class*=" svg-image"] {
  display: block; }

.visuallyhidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto; }

.header {
  padding: 20px; }

.main {
  flex-grow: 1; }

.footer {
  padding: 20px; }

.date {
  width: 380px;
  height: 374px;
  margin-left: 131px;
  margin-top: 73px;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px; }
